.footer {
  width: 100%;
  height: 200px;
  background: #21325e;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

}

.socialMedia svg {
  color: white;
  margin: 20px;
  font-size: 70px;
  cursor: pointer;
}

.footer p {
  color: rgb(255, 255, 255);
}

@media only screen and (max-width: 600px) {
  .footer svg {
    font-size: 50px;
  }
}
